.dropdown {
  position: relative;
  display: inline-block;
  border-radius: 2px;
}

.dropdown-form {
  display: flex;
  flex-direction: row;
  width: 320px;
  height: 44px;
}

.dropbtn-input {
  position: relative;
  width: 320px;
  height: 44px;
  padding-left: 15px;
  padding-right: 0px;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-radius: 4px;
  background: #FFFFFF;
  text-align: start;
  font-family: "MontserratRegular";
  text-transform: none;
  font-style: normal;
  font-size: 14px;
  line-height: 18px;
  color: rgba(31, 32, 65, 0.75);
}

.dropbtn {
  position: absolute;
  width: 40px;
  height: 40px;
  background: #FFFFFF;
  color: rgba(31, 32, 65, 0.75);
  border: none;
  box-sizing: border-box;
  padding: 2px;
  cursor: pointer;
  font-family: "MaterialIcons";
  font-size: 24px;
  line-height: 24px;
  color: rgba(31, 32, 65, 0.5);
  margin-left: 276px;
  margin-top: 2px;
  margin-bottom: 2px;
  margin-right: 2px;
}

.dropbtn:hover, .dropbtn:focus {
  background: #FFFFFF;
}

.dropdown-content {
  display: flex;
  flex-direction: column;
  padding-top: 7px;
  width: 320px;
  height: 159px;
  border: 1px solid rgba(31, 32, 65, 0.25);
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
  box-sizing: border-box;
  box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
  display: none;
  position: absolute;
  background-color: #FFFFFF;
  overflow: auto;
}

.iqdropdown-menu-option {
  padding-left: 15px;
  padding-top: 7px;
  padding-right: 7px;
}

.dropdown a:hover {
  background-color: #FFFFFF;
}

.show {
  display: block;
}

.iqdropdown-menu-option {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-align: center;
  font-family: "MontserratBold";
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #1F2041;
}

.iqdropdown-menu-buttons {
  display: flex;
  flex-direction: row;
  padding-top: 12px;
}

.iqdropdown-menu-apply {
  padding-right: 7px;
  width: 84px;
  height: 15px;
  padding-left: 229px;
  border: none;
  background-color: #FFFFFF;
  font-family: "MontserratBold";
  font-size: 12px;
  line-height: 15px;
  text-transform: uppercase;
  color: #BC9CFF;
}

